import { UseGetMethod } from "@/app/hooks/get_method";
import React, { use, useState } from "react";
import ListOfUsers from "./list_of_users";

interface User {
  name: string;
  surname: string;
  email: string;
  isPayer: boolean;
  dept: number;
}

export default function AdminInterface() {
  const API_URL = process.env.NEXT_PUBLIC_API_URL;
  const { dataHook } = UseGetMethod<User[]>("getusers");

  const [buttonMsg, setButtonMsg] = useState<string | undefined>(undefined);

  const [responseEmail, setResponseEamil] = useState<string>("");
  const [index, setIndex] = useState<number | undefined>(undefined);

  return (
    <main>
      <h1>Admin</h1>
      <ListOfUsers data={dataHook} />
    </main>
  );
}
